import { NextRequest } from "next/server";

type DogInfo = {
  name: string;
  breed: string;
  sex: "male" | "female" | "unknown";
  neutered: boolean | null;
  birthDate: string | null;
};

type OwnerInfo = {
  name: string;
  earthlyBranch: string; // 12ÏßÄÏßÄ: Ïûê/Ï∂ï/Ïù∏/...
};

type RequestBody = {
  dog: DogInfo;
  owner: OwnerInfo;
};

export async function POST(req: NextRequest) {
  let body: RequestBody;
  try {
    body = (await req.json()) as RequestBody;
  } catch {
    return new Response(JSON.stringify({ error: "Invalid JSON" }), { status: 400 });
  }

  if (!body?.dog?.name || !body?.dog?.breed || !body?.owner?.name || !body?.owner?.earthlyBranch) {
    return new Response(JSON.stringify({ error: "missing required fields" }), { status: 400 });
  }

  const openaiKey = process.env.OPENAI_API_KEY;
  const prompt = makePrompt(body);

  let compatibility = fallbackCompatibility(body);

  if (openaiKey) {
    try {
      const completion = await fetch("https://api.openai.com/v1/chat/completions", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${openaiKey}`,
        },
        body: JSON.stringify({
          model: "gpt-4o-mini",
          messages: [
            { role: "system", content: "You are a friendly pet fortune teller. Respond in Korean." },
            { role: "user", content: prompt },
          ],
          temperature: 0.8,
          max_tokens: 500,
        }),
      });
      if (!completion.ok) throw new Error(`OpenAI ${completion.status}`);
      type ChatCompletion = {
        choices?: Array<{
          message?: { content?: string };
        }>;
      };
      const data = (await completion.json()) as unknown as ChatCompletion;
      const content = data?.choices?.[0]?.message?.content?.trim();
      if (content) compatibility = content;
    } catch {
      // keep fallback
    }
  }

  return new Response(JSON.stringify({ compatibility }), {
    headers: { "Content-Type": "application/json" },
  });
}

